<template>
  <el-dialog
    class="grant-authorization"
    title="分享名单"
    :close-on-click-modal="false"
    :visible.sync="visible">
    <div class="grant-authorization-title">
      <span></span>
      <p>{{nameList.name}}</p>
    </div>
    <div class="float-right">
      <el-checkbox v-model="checked">关联子节点</el-checkbox>
      <el-button type="primary" size="small" @click="getCurrentKey">保存</el-button>
    </div>
    <div class="clearfix"></div>
    <el-scrollbar style='height: 400px;'>
      <org-tree ref="tree"
        :treeData="treeData"
        :checkbox='treeCheckBox'
        :checkStrictly='!checked'
        :defaultChecked='defaultChecked'
      ></org-tree>
    </el-scrollbar>
  </el-dialog>
</template>

<script>
  import orgTree from "@/components/baseComponents/orgTree.vue";
  export default{
    components: {
      orgTree
    },
    data(){
      return{
        visible:false,
        nameList:{},
        checked:false,
        treeData:[],
        treeCheckBox:true,//tree复选框是否有
        defaultChecked:[]
      }
    },
    methods:{
      init(row){
        this.visible = true;
        this.nameList=row;
        this.$http({
          url: this.$http.adornUrl(this.API.selectBySharedPerson),
          method: 'post',
          data: this.$http.adornData({
            areaId:this.$store.state.user.areaId,
            nameListId:row.id
          })
        }).then(({data}) => {
          this.treeData=[];
          if(data && data.code===0){
            data.data.forEach(item=>{
              item.id=item.userId;
              item.name=item.userName;
              this.treeData.push(item);
            })
            this.defaultChecked=this.getIsCheckCurrent(this.treeData);
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      getCurrentKey(){//保存选中节点
        this.$http({
          url: this.$http.adornUrl(this.API.saveNameListShare),
          method: 'post',
          data: this.$http.adornData({
            nameListId:this.nameList.id,
            name:this.nameList.name,
            sharingPeople:this.$store.state.user.id,
            sharedPerson:this.$refs.tree.getCheckedKeys().join(',')
          })
        }).then(({data}) => {
          if(data && data.code===0){
            this.$message.success('操作成功');
          }else{
            this.$message.error(data.msg);
          }
        });
      },
      getIsCheckCurrent(arr){//多层树结构中取出是否选中
        const setCheckedKeys=[];
        if(!arr) return;
        const getChild=(arr)=>{
          arr.forEach(item=>{
            if(item.isCheck) setCheckedKeys.push(item.id);
            if(item['children'] && item['children'].length>0){
              getChild(item.children)
            }
          })
          return setCheckedKeys;
        }
        return getChild(arr)
      },

    }
  }
</script>

<style lang="scss" scoped>
  .grant-authorization{
    &-title{
      float: left;
      padding-left: 10px;
      color: #f0992b;
      span{
        display: inline-block;
        width:10px;
        height: 10px;
        background: rgba(#f0992b,0.6);
        box-shadow: 0px 0px 6px 0px #f0992b;border-radius: 50%;}
      p{display: inline-block; padding-left: 10px;}
    }
    .float-right{
      padding-bottom: 10px;
      .el-checkbox{padding-right: 20px;}
    }
  }
</style>



